<template>
  <div class="course-create-or-edit">
    <el-card>
      <div slot="header">
        <!--  -->
        <el-steps :active="activeStep" simple>
          <el-step
            v-for="(item,i) in steps"
            :key="item.id"
            :title="item.title"
            :icon="item.icon"
            @click.native="activeStep = i"
          ></el-step>
        </el-steps>
      </div>
      <!--  -->
      <el-form label-width="80px">
        <!--  -->
        <div v-show="activeStep === 0">
          <el-form-item label="">
            <el-input
              v-model="course.courseName"
            ></el-input>
          </el-form-item>
          <el-form-item label="">
            <el-input
              v-model="course.brief"
            ></el-input>
          </el-form-item>
          <el-form-item label="">
            <el-input
              v-model="course.previewFirstField"
              placeholder="1"
              style="width: 49%;min-width: 300px; margin-right: 15px;"
            >
              <template slot="append">{{ course.previewFirstField.length }}/20</template>
            </el-input>
            <el-input
              v-model="course.previewSecondField"
              placeholder="2"
              style="width: 49%;min-width: 300px;"
            ></el-input>
          </el-form-item>
          <el-form-item label="">
            <el-input
              v-model="course.teacherDTO.teacherName"
            ></el-input>
          </el-form-item>
          <el-form-item label="">
            <el-input
              v-model="course.teacherDTO.description"
            ></el-input>
          </el-form-item>
          <el-form-item label="">
            <el-input-number
              v-model="course.sortNum"
              label=""
              controls-position="right"
            ></el-input-number>
          </el-form-item>
        </div>
        <!--  -->
        <div v-show="activeStep === 1">
          <!--  -->
          <course-image
            v-model="course.courseListImg"
            label=""
            :limit="3"
          ></course-image>
          <course-image
            v-model="course.courseImgUrl"
            label=""
          ></course-image>
        </div>
        <!--  -->
        <div v-show="activeStep === 2">
          <el-form-item label="">
            <el-input
              v-model="course.discounts"
              type="number"
              :min="0"
            >
              <template slot="append"></template>
            </el-input>
          </el-form-item>
          <el-form-item label="">
            <el-input
              v-model="course.price"
              type="number"
              :min="0"
            >
              <template slot="append"></template>
            </el-input>
          </el-form-item>
          <el-form-item label="">
            <el-input
              v-model="course.sales"
              type="number"
              :min="0"
            >
              <template slot="append"></template>
            </el-input>
          </el-form-item>
          <el-form-item label="">
            <el-input
              v-model="course.discountsTag"
            ></el-input>
          </el-form-item>
        </div>
        <!--  -->
        <div v-show="activeStep === 3">
          <el-form-item label="" label-width="120px">
            <el-switch
              v-model="course.activityCourse"
              active-color="#13ce66"
              inactive-color="#ff4949"
            >
            </el-switch>
          </el-form-item>
          <template v-if="course.activityCourse">
            <el-form-item label="">
              <el-date-picker
                v-model="course.activityCourseDTO.beginTime"
                type="datetime"
                placeholder="">
              </el-date-picker>
            </el-form-item>
            <el-form-item label="">
              <el-date-picker
                v-model="course.activityCourseDTO.endTime"
                type="datetime"
                placeholder="">
              </el-date-picker>
            </el-form-item>
            <el-form-item label="">
              <el-input
                v-model="course.activityCourseDTO.amount"
                type="number"
                :min="0"
              >
                <template slot="append"></template>
              </el-input>
            </el-form-item>
            <el-form-item label="">
              <el-input
                v-model="course.activityCourseDTO.stock"
                type="number"
                :min="0"
              >
                <template slot="append"></template>
              </el-input>
            </el-form-item>
          </template>
        </div>
        <!--  -->
        <div v-show="activeStep === 4">
          <el-form-item label="">
            <text-editor v-model="course.courseDescriptionMarkDown"></text-editor>
          </el-form-item>
          <el-form-item label="">
            <el-switch
              v-model="course.status"
              active-color="#13ce66"
              inactive-color="#ff4949"
              :active-value="1"
              :inactive-value="0"
            ></el-switch>
          </el-form-item>
          <el-form-item>
            <el-button
              type="primary"
              @click="handleSave"
            ></el-button>
          </el-form-item>
        </div>
        <!--  -->
        <el-form-item v-if="activeStep !== steps.length - 1">
          <el-button
            @click="activeStep++"
          ></el-button>
        </el-form-item>
      </el-form>
    </el-card>
  </div>
</template>

<script>
import CourseImage from './CourseImage'
import { saveOrUpdateCourse, getCourseById } from '@/services/course'
import TextEditor from '@/components/TextEditor'

export default {
  name: 'CourseCreateOrEdit',
  props: {
    isEdit: {
      type: Boolean,
      default: false
    },
    courseId: {
      type: [String, Number]
    }
  },
  components: {
    CourseImage,
    TextEditor
  },
  created () {
    //  courseId 
    if (this.isEdit) {
      this.loadCourse()
    }
  },
  data () {
    return {
      // 
      activeStep: 0,
      // 
      steps: [
        { id: 1, title: '', icon: 'el-icon-edit' },
        { id: 2, title: '', icon: 'el-icon-upload' },
        { id: 3, title: '', icon: 'el-icon-picture' },
        { id: 4, title: '', icon: 'el-icon-picture' },
        { id: 5, title: '', icon: 'el-icon-picture' }
      ],
      // 
      imageUrl: '',
      // 
      //   -  ID 
      course: {
        // id: 0,
        // 
        courseName: '',
        // 
        brief: '',
        teacherDTO: {
          // id: 0,
          // courseId: 0,
          // 
          teacherName: '',
          teacherHeadPicUrl: '',
          position: '',
          // 
          description: ''
        },
        // 
        courseDescriptionMarkDown: '<h3></h3>',
        // 
        price: 0,
        // 
        discounts: 0,
        priceTag: '',
        // 
        discountsTag: '',
        isNew: true,
        isNewDes: '',
        // 
        courseListImg: '',
        // 
        courseImgUrl: '',
        // 
        sortNum: 0,
        // 1
        previewFirstField: '',
        // 2
        previewSecondField: '',
        //  0 1 
        status: 0,
        // 
        sales: 0,
        // 
        activityCourse: false,
        activityCourseDTO: {
          // id: 0,
          // courseId: 0,
          // 
          beginTime: '',
          // 
          endTime: '',
          // 
          amount: 0,
          // 
          stock: 0
        },
        autoOnlineTime: ''
      }
    }
  },
  methods: {
    //  ID 
    async loadCourse () {
      const { data } = await getCourseById(this.courseId)
      if (data.code === '000000') {
        // - 
        if (!data.data.activityCourse) {
          data.data.activityCourseDTO = {
            beginTime: '',
            endTime: '',
            amount: 0,
            stock: 0
          }
        }
        this.course = data.data
      }
    },
    // 
    async handleSave () {
      const { data } = await saveOrUpdateCourse(this.course)
      if (data.code === '000000') {
        this.$message.success(`${this.isEdit ? '' : ''}`)
        this.$router.push({
          name: 'course'
        })
      }
    }
  }
}
</script>

<style lang="scss" scoped>
.el-step {
  cursor: pointer;
}
</style>
